html, body, div, span,
h1, h2, h3, h4, h5, h6, p, blockquote, a,
img, dl, dt, dd, ol, ul, li,
fieldset, form, label,
table, caption, tbody, tfoot, thead, tr, th, td,
footer, header,
menu, nav, section {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  vertical-align: baseline; }

footer, header, menu, nav, section {
  display: block; }

body {
  line-height: 1;
  background: #eeeeee; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

button {
  outline: none;
  cursor: pointer; }

a {
  outline: none;
  text-decoration: none; }

.cf {
  content: " ";
  display: table; }
  .cf:after, .cf:before {
    clear: both; }

.main {
  width: 1415px;
  margin: 0 auto; }
  .main_left {
    width: 319px;
    height: 100px;
    float: left;
    background: #303841;
    padding: 48px 0 250px; }
  .main_right {
    margin-left: 319px;
    background: #ffffff;
    height: 100px; }
  .main__title {
    font-family: "Open Sans", sans-serif;
    color: #ffc12d;
    font-size: 28px;
    letter-spacing: 0.7px;
    padding-left: 27px;
    margin-bottom: 57px; }

.menu {
  padding: 0 17px 0 18px; }
  .menu__link {
    display: block;
    padding: 17px 0px 15px;
    font-family: "Open Sans", sans-serif;
    font-size: 17px;
    font-weight: 300;
    color: #ffffff;
    border-bottom: 2px solid rgba(255, 255, 255, 0.07); }
